export function ConvertDate(dateString) {
    if (dateString !== undefined) {
        return new Date(dateString).toLocaleString();
    }
    else {
        return undefined;
    }
}

export function GetAuthorsInitial(author) {
    if (author !== undefined) {
        var authorSplit = author.split(' ');
        return authorSplit[0].charAt(0) + authorSplit[authorSplit.length - 1].charAt(0);
    }
    else {
        return undefined;
    }
}